.weather {
  width: 70%;
  border-radius: 6px;
  box-shadow: 10px -2px 20px 2px rgb(0 0 0 / 30%);
  color: #fff;
  background-color: #333;
  margin: 20px auto 0 auto;
  padding: 0 20px 20px 20px;
}

.top,
.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.city {
  font-weight: 600;
  font-size: 24px;
  line-height: 1;
  margin: 0;
  letter-spacing: 1px;
}

.weather-description {
  font-weight: 400;
  font-size: 20px;
  line-height: 1;
  margin: 0;
}

.weather-icon {
  width: 100px;
}

.temerature {
  font-weight: 600;
  font-size: 3rem;
  width: auto;
  letter-spacing: -5px;
  margin: 10px 0;
}

.details {
  width: 100%;
  padding-left: 40px;
}

.parameter-row {
  display: flex;
  justify-content: space-between;
  line-height: 24px;
}

.parameter-label {
  text-align: left;
  font-weight: 400;
  font-size: 16px;
}
.parameter-value {
  text-align: right;
  font-weight: 600;
  font-size: 16px;
}
.parameter-label.top {
  border-bottom: 1px solid #bfc;
}
